.classification {
  height: 75px;
  width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;

  border-right: 1px solid rgba(188, 188, 188, 0.162);
  transition: background-color .6s ease;

  &:hover {
    background-color: #e9e9e96f;
  }

  font-size: 35px;
  color: rgb(127, 127, 127);
  letter-spacing: 3px;
  font-family: 'SF-Pro-Light';

  cursor: pointer;
}

sup {
  font-size: 17px;
}

input[type='radio'] {
  display: none;
  cursor: pointer;
}

input[type='radio']:checked + .classification {
  background-color: #89e7ff41;
}