body {
  margin: 0;
  font-family: 'SF-Pro';
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.top {
  margin: 0;
  width: 100vw;
  height: 28vh;
  background-color: rgb(235, 235, 235);

  background-image: url(wd.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: rgba(255, 255, 255, 0.7);
  background-blend-mode:color-dodge;

  display: flex;
  flex-direction: column;
  justify-content: center;

  border-bottom: 1px solid rgba(188, 188, 188, 0.162);
}

#header {
  margin: 0;
  margin-left: 100px;
  font-family: 'SF-Pro-Bold';
  font-size: 100px;
  /* font-weight: 500; */
  letter-spacing: -5px;
}

#header2 {
  margin: 0;
  margin-left: 110px;
  font-family: 'SF-Pro-Light';
  font-size: 20px;
  letter-spacing: 0px;
}

#thin {
  margin: 0;
  font-family: 'SF-Pro-Light';
  font-size: 100px;
  font-weight: 100;
  letter-spacing: -5px;
}

.NavItems {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100vw;
  height: 75px;
  border-bottom: 1px solid rgba(188, 188, 188, 0.162);
  
}

input[type= "number" ]::-webkit-inner-spin-button, 
  input[type= "number" ]::-webkit-outer-spin-button {
                -webkit-appearance: none;
                margin: 0;
  }
