input[type="number"] {
    outline: none;
    font-family: 'SF-Pro-Light';
    /* font-family: 'Times New Roman', Times, serif; */
    font-size: 25px;
    width: 100px;
    height: 65px;
    text-align: center;
    border-radius: 15px;
    border: 1px solid rgba(205, 205, 205, 0.7);
    transition-duration: .6s;
    &:hover {
        box-shadow: 1px 1px 1px 1px rgba(205, 205, 205, 0.5);
    }
}

.decisions {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    font-size: 25px;
    font-family: 'SF-Pro-Light';
    /* font-family: 'Times New Roman', Times, serif; */

}

.eqPositions {
    margin-top: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
}

.theXs {
    margin-top: 55px;
    width: 750px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

